.TodoListItem{
    padding : 1rem;
    display : flex;
    align-items : center;
    &:nth-chile(even) {
        background : #f3e563;
    }
    .checkbox  {
        cursor : pointer;
        flex : 1; // 차지할 수 있는 영역 모두 차지
        display : flex;
        align-tiems : center;
        svg{
            //아이콘
            font-size : 1.5rem;
        }
        .text {
            margin-left : 0.5rem;
            flex : 1; 
        }
        //체크되었을 때 보여 줄 스타일
        &.checked {
            svg{
                color : #78d5ec;
            }
            .text{
                color : #adb5bd;
                text-decoration : line-through;
            }
        }
    }
    .remove {
        display : flex;
        align-items:center;
        font-size : 1.5rem;
        color : #ff6b6b;
        cursor : pointer;
        &:hover{
            color : #ff8787;
        }
    }

    //앨리먼트 사이사이에 테두리 그리기
    & + & {
        border-top : 1px solid #dee2e6;
    }
}